import React from 'react'
import {BrowserRouter, Switch, Route} from 'react-router-dom'
import {Provider} from "react-redux"

import Product from './pages/Product'
import Home from './pages/Home'
import Login from './pages/Login'
import RegisterCustomer from './pages/RegisterCustomer'
import RegisterSeller from './pages/RegisterSeller'
import Cart from './pages/Cart'
import Detail from './pages/Detail'
import Checkout from './pages/Checkout'
import Profile from './pages/Profile'
import ProfileAddress from './pages/ProfileAddress'

import PrivateRoute from './components/PrivateRoute'

import store from "./redux/store"

class App extends React.Component{
  render(){
    return(
      <Provider store={store}>
      <BrowserRouter>
        <Switch>
          <Route path="/" component={Home} exact/>
          <Route path='/login' render ={(props)=><Login {...props} />} />
          <Route path="/register/seller" component={RegisterSeller}/>
          <Route path="/register" component={RegisterCustomer}/>
          <Route path="/detail" component={Detail} />
          {/* <Route path="/profile/cart" component={Cart}/> */}
          <Route path="/profile/checkout" component={Checkout}/>

          <PrivateRoute path="/admin/product" component={Product} />
          <PrivateRoute path="/profile/cart">
            <Cart />
          </PrivateRoute>
          {/* <PrivateRoute path="/profile/checkout" component={Checkout}/> */}
          <PrivateRoute path="/profile/address"> 
            <ProfileAddress />
          </PrivateRoute>
          <PrivateRoute path="/profile">
             <Profile />
          </PrivateRoute>
        </Switch>
      </BrowserRouter>
      </Provider>
    )
  }
}

export default App