import React from 'react'
import { Button } from 'reactstrap'
import plus from '../assets/img/plus.png'
import min from '../assets/img/min.png'

function Counter () {
  const [counter, setCounter] = React.useState(0)

  return (
    <div>
      <div className='d-flex'>
        <span><Button className='count rounded-pill btn btn-secondary' onClick={() => setCounter(counter - 1)}><img src={min} /></Button></span>
        <span className='num ml-4 mr-4'>{counter}</span>
        <span><Button className='count1 rounded-pill btn btn-outline-secondary btn-light' onClick={() => setCounter(counter + 1)}><img src={plus} /></Button></span>
      </div>
    </div>
  )
}

export default Counter
