import React from 'react'
import {Link} from 'react-router-dom'
import {Button, ButtonGroup, Container, Form, Input, Alert} from 'reactstrap'
import logo from '../assets/img/logo.png'
import {connect} from 'react-redux'
import auth from '../redux/actions/auth'

class RegisterCustomer extends React.Component {

    state = {
        name: '',
        email: '',
        password: '',
    }
    registerCustomer = (e) => {
        e.preventDefault()
        const {name, email, password} = this.state
        const data = {
            name,
            email,
            password
        }
        this.props.registerCustomer(data)
    }
    onChangeText =(e) => {
        this.setState({[e.target.name]:e.target.value})
    }
    componentDidUpdate(){
        if(this.props.auth.isRegister){
            this.props.history.push('/login')
        }
    }

    render(){
    const {isError, alertMsg} = this.props.auth
    return(
        <>
            <Container className="vh-100 vw-100 d-flex justify-content-center align-items-center">
                <div>
                <div class="header text-center">
                    <img src={logo} className="mt-5 mb-5" alt="logo" />
                    <div className="font-weight-bold mb-5">
                        <span>Please Sign up with your account</span>
                    </div>
                </div>
                <div className="text-center">
                    <ButtonGroup className="mb-5" color="none">
                        <Button outline color="light" className="btnlog cus">Customer</Button>
                        <Link to="/register/seller"><Button outline color="light" className="btnlog sel">Seller</Button></Link>
                    </ButtonGroup>
                </div>
                <Alert color={isError?'danger':'success'} isOpen={isError || alertMsg!==''}>{alertMsg}</Alert>
                    <Form onSubmit={this.registerCustomer}>
                        <div className="auth text-center">
                            <Input onChange={this.onChangeText} name="name" type="name" placeholder="Name" className="mb-3"/>
                            <Input onChange={this.onChangeText} name="email" type="email" placeholder="Email" className="mb-3"/>
                            <Input onChange={this.onChangeText} name="password" type="password" placeholder="Password" className="mb-5"/>
                        </div>
                        <Button className="auth rounded-pill mb-3 btn-danger" block>Register</Button>
                    </Form>
                    <div className="text-center">
                        Already have Blanja Account? <Link to="/login" className="text-danger">Login</Link>
                    </div>
                </div>
            </Container>

        </>
    )
    }   
}

const mapStateToProps = state => ({auth: state.auth})

const mapDispatchToProps = {
  registerCustomer: auth.registerCustomer
}

export default connect(mapStateToProps, mapDispatchToProps)(RegisterCustomer) 