import http from '../../helpers/http'
import qs from 'querystring'

export default {
    addAddress: (token, data)=>{
        return {
            type: 'ADD_ADDRESS',
            payload: http(token).post(`users/address`, qs.stringify(data))
        }
    },
    getAddress: (token)=>{
        return {
            type: 'GET_ADDRESS',
            payload: http(token).get(`profile/address`)
        }
    },
    clearMessage: () => {
        return {
            type: 'CLEAR_PROFILE_MESSAGE'
        }
    }
}