import React from 'react'
import { Container, Row } from 'reactstrap'
import axios from 'axios'
import tengah1 from '../assets/img/tengah1.png'
import tengah2 from '../assets/img/tengah2.png'
import tengah3 from '../assets/img/tengah3.png'
import tengah4 from '../assets/img/tengah4.png'
import tengah5 from '../assets/img/tengah5.png'

class SliderCat extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      data: []
    }
  }

  async componentDidMount () {
    await this.getData()
  }

  async getData () {
    const { data } = await axios.get('http://localhost:8080/category')
    this.setState({ data: data.data })
  }

  render () {
    return (
      <>
        <Container>
          <Row className='justify-content-center'>
            <div className='mr-2 ml-2'>
              <img src={tengah1} />
            </div>
            <div className='mr-2 ml-2'>
              <img src={tengah2} />
            </div>
            <div className='mr-2 ml-2'>
              <img src={tengah3} />
            </div>
            <div className='mr-2 ml-2'>
              <img src={tengah4} />
            </div>
            <div className='mr-2 ml-2'>
              <img src={tengah5} />
            </div>
          </Row>
        </Container>
      </>
    )
  }
}

export default SliderCat
