import React, { Component } from 'react'
import {Container, Row, Col, Button, 
    Form, Input, FormGroup, Label} from 'reactstrap'
import Navibar from '../components/Navibar'
import profile from '../assets/img/default.jpg'
import pen from '../assets/img/pen.svg'
import adr from '../assets/img/adr.svg'
import btnuse from '../assets/img/user.svg'
import bag from '../assets/img/bag.svg'

import {connect} from 'react-redux'
import profileAction from '../redux/actions/profile'
import {Link} from 'react-router-dom'

const {REACT_APP_URL} = process.env

class Profile extends Component {
    state = {
        full_name: '',
        email: '',
        phone: '',
        gender: '',
        year: '',
        month: '',
        date: ''
    }

    componentWillMount(){
        this.props.getProfile(this.props.auth.token)
    }

    componentDidUpdate(){
        if (this.props.profile.alertMsg !== '') {
            this.props.clearMessage()
            const {data} = this.props.profile
            const birth = data.birthday.split('-')
            this.setState({
                full_name: data.name, 
                email: data.email,
                phone: data.phone,
                gender: data.gender,
                year: birth[2],
                month: birth[0],
                date: birth[1]
            })
        }
    }

    updateProfile = async (e) =>{
        e.preventDefault()
        const {full_name, email, phone, gender, year, month, date} = this.state
        const birthday = `${month}-${date}-${year}`
        const name = full_name
        const data = {
            name,
            email,
            phone,
            gender,
            birthday
        }
        this.props.updateProfile(this.props.auth.token, data)
        await this.props.getProfile(this.props.auth.token)
    }

    updateImage = async (e) => {
        const form = new FormData();
        form.append('picture', e.target.files[0])
        this.props.updateImage(this.props.auth.token, form)
        await this.props.getProfile(this.props.auth.token)
    }
    
    onChangeText =(e) => {
        this.setState({[e.target.name]:e.target.value})
    }

    onChangeGender =(e) => {
        this.setState({[e.target.name]:e.target.id})
    }
    

    render() {
        let date = new Date()
        let tahun = date.getFullYear()
        let temp = []
        let tempdate = []
        
        const listmonth = ['Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember']

        for (let i = tahun; i >= 1900; i--) {
                temp += `${i},`;
            }
        for (let i = 1; i <= 31; i++) {
            tempdate += `${i},`; 
        }

        let listyear = temp.split(',')
        let listday = tempdate.split(',')
        
        const {data} = this.props.profile
        let item = ''
        {Object.keys(data).length&&(
         item = data.birthday.split('-')
        )}
        return (
            <div>
            {Object.keys(data).length&&(
            <>
            <Navibar />
            <Container fluid className="full">
                <Row>
                   <Col md={3}>
                        <div className="side d-flex">
                            <img className="img-side rounded-circle" src={data.picture===null?profile:REACT_APP_URL.concat(data.picture)}/>
                            <div className="ml-3">
                                <div className="font-weight-bold mt-2 name">{this.state.full_name}</div> 
                                <div><img src={pen} className="mr-1"/>Ubah profile</div>
                            </div>
                        </div>
                        <div>
                            <div className="font-weight-bold mb-4">
                                <Button color="primary" className="btnside badge-pill mr-3"><img src={btnuse}/></Button>
                                My Account
                            </div>
                            <Link to="/profile/address">
                            <div className="text-secondary mb-4">
                               <Button color="warning" className="btnside badge-pill mr-3"><img src={adr}/></Button>
                                Shipping Address
                            </div>
                            </Link>
                            <Link to="/profile/">
                            <div className="text-secondary mb-4">
                                <Button color="danger" className="btnside badge-pill mr-3"><img src={bag}/></Button>
                                My Order
                            </div>
                            </Link>
                        </div>
                   </Col>
                   <Col md={9} className="forminput vh-100">
                       <div className="input-center">
                       <h3>My Profile</h3>
                       <h6 className="text-secondary">Manage your profile information</h6>
                       <hr />
                       <Row>
                           <Col md={3}>
                                <div className="text-secondary text-right">
                                    <div className="dist">Name</div>
                                    <div className="dist">Email</div>
                                    <div className="dist">Phone Number</div>
                                    <div className="dist">Gender</div>
                                    <div className="dist">Date of birth</div>
                                </div>
                           </Col>
                           <Col md={6}>
                                <Form onSubmit={this.updateProfile}>
                                    <Input onChange={this.onChangeText} name="full_name" type="name" className="dark dist-form1 name" value={this.state.full_name} />
                                    <Input onChange={this.onChangeText} name="email" type="email" className="dark dist-form" value={this.state.email} />
                                    <Input onChange={this.onChangeText} name="phone" type="name" className="dark dist-form2" value={this.state.phone} />
                                    <Row className="dist-form">
                                        <Col md={4}>
                                            <FormGroup check>
                                                <Input onChange={this.onChangeGender} type="radio" checked={this.state.gender==='laki-laki'} className="form-check-input" name="gender" id="laki-laki"/> 
                                                <Label check className="text-secondary">Laki-laki</Label>
                                            </FormGroup>
                                        </Col>
                                        <Col md={4}>
                                            <FormGroup check>
                                                <Input onChange={this.onChangeGender} type="radio" checked={this.state.gender==='perempuan'} className="form-check-input" name="gender" id="perempuan"/> 
                                                <Label check className="text-secondary">Perempuan</Label>
                                            </FormGroup>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={4}>
                                            <FormGroup>
                                                <select onChange={this.onChangeText} id="inputState" name="date" className="form-control">
                                                  <option selected>{this.state.date}</option>
                                                  {listday.map(item => {
                                                    return(
                                                    <option>{item}</option>
                                                    )})}
                                                </select>
                                              </FormGroup>
                                        </Col>
                                        <Col md={4}>
                                        <FormGroup>
                                                <select onChange={this.onChangeText} id="inputState" name="month" className="form-control">
                                                  <option selected>{this.state.month}</option>
                                                  {listmonth.map(item => {
                                                    return(
                                                    <option>{item}</option>
                                                    )})}
                                                </select>
                                              </FormGroup>
                                        </Col>
                                        <Col md={4}>
                                        <FormGroup>
                                                <select onChange={this.onChangeText} id="inputState" name="year" className="form-control">
                                                  <option selected>{this.state.year}</option>
                                                  {listyear.map(item => {
                                                    return(
                                                    <option>{item}</option>
                                                    )})}
                                                </select>
                                              </FormGroup>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={4} className="mt-4">
                                            <Button color="danger" className="rounded-pill btn-save">Save</Button>
                                        </Col>
                                    </Row>
                                </Form>
                           </Col>
                           <Col md={3} className="">
                               <div className="d-flex justify-content-center">
                                <img className="img-profile rounded-circle" src={data.picture!==null?REACT_APP_URL.concat(data.picture):profile}/>
                               </div>
                                <label className="mt-3 rounded-pill btn-save btn btn-light btn-outline-secondary">
                                    <span>Select Image</span>
                                    <Input onChange={this.updateImage} style={{display:'none'}} type="file" accept=".jpg, .png, .jpeg" />
                                </label>
                           </Col>
                       </Row>
                       </div>
                   </Col> 
                </Row>
            </Container>
        </>
        )}
    </div>
        )
    }
}

const mapStateToProps = state => ({
    auth: state.auth,
    profile: state.profile
})

const mapDispatchToProps = {
    getProfile: profileAction.getProfile,
    updateProfile: profileAction.updateProfile,
    updateImage: profileAction.updateImage,
    clearMessage: profileAction.clearMessage
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile)