import React from 'react'
import {Container, Col, Row, Input, Button, Alert} from 'reactstrap'
import {Link} from 'react-router-dom'
import Navibar from '../components/Navibar'
import hijau from '../assets/img/hijau.jpg'

class Checkout extends React.Component{

    render(){
        return(
            <>
            <Navibar />
            <Container>
            <h3 className="title mb-3">Checkout</h3>
            <h6 className="text-secondary mb-4">Shipping Address</h6>
                <Row>
                <Col sm={12} md={8}>
                        <div className="select shadow-sm border mb-3">    
                            <div className="mb-3">
                                <div className="h5 mb-3">Andreas Jane</div>
                                <article>Perumahan Sapphire Mediterania, Wiradadi, Kec. Sokaraja, Kabupaten Banyumas, Jawa Tengah, 53181 [Tokopedia Note: blok c 16] Sokaraja, Kab. Banyumas, 53181</article>
                            </div>
                            <Button className="btnadd btn btn-light btn-outline-secondary rounded-pill">Chose another address</Button>
                        </div>
                        <div className="select d-flex shadow-sm border mb-2">    
                            <img className="img-thumbnail thumb" src={hijau} />
                            <div className="ml-3 mt-2">Men's formal suit - Black<div className="text-secondary sto">Zalora cloth</div></div>
                            <div className="del position-absolute mt-4 font-weight-bold">$20.0</div> 
                        </div>
                        <div className="select d-flex shadow-sm border mb-2">    
                            <img className="img-thumbnail thumb" src={hijau} />
                            <div className="ml-3 mt-2">Men's formal suit - Black<div className="text-secondary sto">Zalora cloth</div></div>
                            <div className="del position-absolute mt-4 font-weight-bold">$20.0</div> 
                        </div>
                    </Col>
                    <Col sm={12} md={4}>
                    <div className="select d-flex shadow-sm border">    
                        <div className="but">
                            <div className="h6">Shopping Summary</div>
                            <div className="mt-3 mb-1"><span className="text-secondary">Order</span><span className="del position-absolute font-weight-bold">$40.0</span></div>
                            <div className="mb-4"><span className="text-secondary">Delivery</span><span className="del position-absolute font-weight-bold">$5.0</span></div>
                            <hr />
                            <div className="mb-4"><span className="font-weight-bold">Total Price</span><span className="del position-absolute font-weight-bold text-danger">$45.0</span></div>
                            <Button color="danger" className="rounded-pill but" block>Select Payment</Button>
                        </div>
                    </div>
                    </Col>
                </Row>
                </Container>
            </>
        )
    }
}

export default Checkout