import React from 'react'
import {Container, Col, Row, Input, Button, Alert} from 'reactstrap'
import Counter from '../components/Counter'
import Navibar from '../components/Navibar'
import { Link } from 'react-router-dom';
import hijau from '../assets/img/hijau.jpg'

import {connect} from 'react-redux'
import authAction from '../redux/actions/auth'
import cartAction from '../redux/actions/cart'

class Cart extends React.Component{
    
    componentDidMount(){
        this.props.getCart(this.props.auth.token)
      }

    render(){
        const {cart} = this.props.cart
        // const sum = 
        // cart.map(item => {
        //     return item.total_price
        //   })
        //   const summary = sum.reduce((total, value) => total + value, 0)
        return(
            <>
            <Navibar/>
             <Container>
                 <h1 className="title">My Bag</h1>
                <Row>
                    <Col sm={12} md={8}>
                        <div className="select d-flex shadow-sm border mb-3">    
                            <div>
                                <Input className="input" addon type="checkbox"/>
                                <span className="ml-3">Select all items</span>
                                <span className="text-secondary ml-2">({cart.length+1} items selected)</span>
                            </div>
                            <div className="del position-absolute"><Link to='#' className="text-danger">Delete</Link></div> 
                        </div>
                        {/* {cart.map(item => { */}
                            {/* return( */}
                        <div className="select d-flex shadow-sm border mb-2">    
                            <Input className="check mt-4" addon type="checkbox"/>
                            <img className="img-thumbnail thumb ml-3" src={hijau} />
                            <div className="ml-3 mt-2">{cart.product}<div className="text-secondary sto">Zalora cloth</div></div>
                            <div className="ml-5 mt-2">quantity: {cart.quantity}</div>
                            <div className="del position-absolute mt-4">{cart.price}</div> 
                        </div>
                            {/* )
                        })} */}
                    </Col>
                    <Col sm={12} md={4}>
                    <div className="select d-flex shadow-sm border">    
                        <div className="but">
                            <div className="">Shopping Summary</div>
                    <div className="mt-3 mb-4"><span className="text-secondary">Total price</span><span className="del position-absolute text-black">{cart.total_price}</span></div>
                        <Button color="danger" className="rounded-pill but" block>Buy</Button>
                        </div>
                    </div>
                    </Col>
                </Row>
             </Container>
            </>
        )
    }
}
const mapStateToProps = state => ({
    auth: state.auth,
    cart: state.cart
})

const mapDispatchToProps = {
  clearMessage: authAction.clearMessage,
  getCart: cartAction.getCart
}

export default connect(mapStateToProps, mapDispatchToProps)(Cart)