const initialState = {
    isLogin: false,
    isError: false,
    isRegister: false,
    alertMsg: '',
    token: ''
}

export default (state=initialState, action) => {
    switch (action.type) {
        case 'AUTH_USER_FULFILLED':{ 
            return {
                ...state,
                isLoading: false,
                isLogin: true,
                alertMsg: 'Login Successfully',
                token: action.payload.data.Token
            }
        }
        case 'AUTH_USER_REJECTED':{ 
            return {
                ...state,
                isError: true,
                alertMsg: 'Wrong email or password'
            }
        }
        case 'AUTH_USER_PENDING' : {
            return {
              ...state,
              isLoading: true
            }
          }
        case 'REGISTER_CUSTOMER_FULFILLED':{ 
            return {
                ...state,
                isRegister: true,
                isLoading: false,
                alertMsg: 'Register Successfully'
            }
        }
        case 'REGISTER_CUSTOMER_REJECTED':{ 
            return {
                ...state,
                isError: true,
                alertMsg: 'Email already used'
            }
        }
        case 'REGISTER_CUSTOMER_PENDING' : {
            return {
              ...state,
              isLoading: true
            }
          }
          case 'REGISTER_SELLER_FULFILLED':{ 
            return {
                ...state,
                isRegister: true,
                isLoading: false,
                alertMsg: 'Register Successfully'
            }
        }
        case 'REGISTER_SELLER_REJECTED':{ 
            return {
                ...state,
                isError: true,
                alertMsg: 'Email already used'
            }
        }
        case 'REGISTER_SELLER_PENDING' : {
            return {
              ...state,
              isLoading: true
            }
          }  
        case 'LOGOUT_USER' :{
            return {
                ...state,
                isLogin: false,
                isError: false,
                alertMsg: 'Logout Succesfully'
            }
        }
        case 'CLEAR_MESSAGE': {
            return {
                ...state,
                alertMsg: ''
            }
        }
        default:{
            return state
        }
    }
}