import React from 'react'
import {Link} from 'react-router-dom'
import {Button, ButtonGroup, Container, Form, Input} from 'reactstrap'
import logo from '../assets/img/logo.png'

function RegisterSeller() {
    
    return(
        <>
            <Container className="vh-100 vw-100 d-flex justify-content-center align-items-center">
                <div>
                <div class="header text-center">
                    <img src={logo} className="mt-5 mb-5" alt="logo" />
                    <div className="font-weight-bold mb-5">
                        <span>Please Sign up with your account</span>
                    </div>
                </div>
                <div className="text-center">
                    <ButtonGroup className="mb-5" color="none">
                        <Link to="/register"><Button outline color="light" className="btnlog cus">Customer</Button></Link>
                        <Button outline color="light" className="btnlog sel">Seller</Button>
                    </ButtonGroup>
                </div>
                    <Form>
                        <div className="auth text-center">
                            <Input type="name" placeholder="Name" className="mb-3"/>
                            <Input type="name" placeholder="Store" className="mb-3"/>
                            <Input type="number" placeholder="Phone" className="mb-3"/>
                            <Input type="email" placeholder="Email" className="mb-3"/>
                            <Input type="password" placeholder="Password" className="mb-5"/>
                        </div>
                        <Button className="auth rounded-pill mb-3 btn-danger" block>Login</Button>
                    </Form>
                    <div className="text-center">
                        Already have Blanja Account? <Link to="/login" className="text-danger">Login</Link>
                    </div>
                </div>
            </Container>

        </>
    )   
}

export default RegisterSeller