const initialState = {
    address : [],
    isLoading: false,
    isError: false,
    alertMsg: '',
    isSuccess: false
}

export default (state=initialState, action)=>{
    switch(action.type){
        case 'GET_ADDRESS_FULFILLED' : {
            return {
                ...state,
                isLoading: false,
                alertMsg: action.payload.data.message,
                address: action.payload.data.data
            }
        }
        case 'CLEAR_PROFILE_MESSAGE' :{
            return{
                ...state,
                alertMsg: ''
            }
        }
        case 'GET_ADDRESS_PENDING' : {
            return {
                ...state,
                isLoading: true
            }
        }
        case 'GET_ADDRESS_REJECTED' : {
            return {
                ...state,
                isLoading: false,
                isError: true,
                alertMsg: 'There is an error at request data'
            }
        }
        case 'ADD_ADDRESS_FULFILLED' : {
            return {
                ...state,
                isLoading: false,
                isSuccess: true
            }
        }
        case 'ADD_ADDRESS_PENDING' : {
            return {
                ...state,
                isLoading: true
            }
        }
        case 'ADD_ADDRESS_REJECTED' : {
            return {
                ...state,
                isLoading: false,
                isError: true,
                alertMsg: 'There is an error at request data'
            }
        }
        default : {
            return state
        }
    }
   
}