const initialState = {
    data : {},
    isLoading: false,
    isError: false,
    alertMsg: '',
    isSuccess: false
}

export default (state=initialState, action)=>{
    switch(action.type){
        case 'GET_PROFILE_FULFILLED' : {
            return {
                ...state,
                isLoading: false,
                alertMsg: action.payload.data.message,
                data: action.payload.data.data[0]
            }
        }
        case 'CLEAR_PROFILE_MESSAGE' :{
            return{
                ...state,
                alertMsg: ''
            }
        }
        case 'GET_PROFILE_PENDING' : {
            return {
                ...state,
                isLoading: true
            }
        }
        case 'GET_PROFILE_REJECTED' : {
            return {
                ...state,
                isLoading: false,
                isError: true,
                alertMsg: 'There is an error at request data'
            }
        }
        
        case 'UPDATE_PROFILE_FULFILLED' : {
            return {
                ...state,
                isLoading: false,
                isSuccess: true
            }
        }
        case 'UPDATE_PROFILE_PENDING' : {
            return {
                ...state,
                isLoading: true
            }
        }
        case 'UPDATE_PROFILE_REJECTED' : {
            return {
                ...state,
                isLoading: false,
                isError: true,
                alertMsg: 'There is an error at request data'
            }
        }
        case 'UPDATE_IMAGE_FULFILLED' : {
            return {
                ...state,
                isLoading: false,
                isSuccess: true
            }
        }
        case 'UPDATE_IMAGE_PENDING' : {
            return {
                ...state,
                isLoading: true
            }
        }
        case 'UPDATE_IMAGE_REJECTED' : {
            return {
                ...state,
                isLoading: false,
                isError: true,
                alertMsg: 'There is an error at request data'
            }
        }
        default : {
            return state
        }
    }
}