import http from '../../helpers/http'
import qs from 'querystring'

export default{
    login: (data) => ({
        type: 'AUTH_USER',
        payload: http().post('auth/login', qs.stringify(data))       
    }),
    registerCustomer:(data) => ({
        type: 'REGISTER_CUSTOMER',
        payload: http().post('auth/signup/customer', qs.stringify(data))
    }),
    registerSeller:(data) => ({
        type: 'REGISTER_SELLER',
        payload: http().post('auth/signup/seller', qs.stringify(data))
    }),
    logout: ()=>({
        type: 'LOGOUT_USER'
    }),
    clearMessage: ()=>({
        type: 'CLEAR_MESSAGE'
    })
}