import React from 'react'
import Navibar from '../components/Navibar'
import {Col, Row, Container, Button, Card, CardBody, CardTitle,
    CardSubtitle, CardImg, FormGroup} from 'reactstrap'
import {Link} from 'react-router-dom'
import det1 from '../assets/img/td1.png'
import s2 from '../assets/img/td2.png'
import s3 from '../assets/img/td3.png'
import s4 from '../assets/img/td4.png'
import s5 from '../assets/img/td5.png'
import s6 from '../assets/img/td1.png'
import star from '../assets/img/star.png'
import Counter from '../components/Counter'
import pol from "../assets/img/pol.png"

import {connect} from 'react-redux'
import homeAction from '../redux/actions/home'
import cartAction from '../redux/actions/cart'

class Detail extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            data1: [],
            id: '',
            quantity: 0
        }
    }

    componentDidMount(){
        this.props.getHome()
    }

    componentDidUpdate(){
        const{id, quantity} = this.state
        const data ={
            id,
            quantity
        }
        this.props.addCart(this.props.auth.token, data)
    }
    
    render(){
        const {isLoading, data, isError, alertMsg} = this.props.homeAction
        return(
            <>
                <Navibar />
                <Container>
                    <div className="text-secondary mt-5 mb-5">Home &gt; category &gt; T-shirt</div>
                    <div>
                    <Row>
                        <Col sm={12} md={4}>
                            <div>
                                <img className="detgam" src={det1} alt="det1" />
                            </div>
                            <div className="d-flex justify-content-between ml-1 mt-3">
                                <img className="detcil mr-2" src={s2} alt="s2"/>
                                <img className="detcil mr-2" src={s3} alt="s3"/>
                                <img className="detcil" src={s4} alt="s4"/>
                                <img className="detcil ml-2" src={s5} alt="s5"/>
                                <img className="detcil ml-2" src={s6} alt="s6"/>
                            </div>
                        </Col>
                        <Col md={8}>
                            <div className="ml-3">
                                <h2>Baju muslim pria</h2>
                                <h6 className="text-secondary">Zalora Cloth</h6>
                                <div>
                                    <img className="mr-1" src={star}/>
                                    <img className="mr-1" src={star}/>
                                    <img className="mr-1" src={star}/>
                                    <img className="mr-1" src={star}/>
                                    <img className="mr-1" src={star}/>
                                    <span className="text-secondary total ml-1">(10)</span>
                                </div>
                                <div className="mt-4">
                                    <div className="text-secondary">Price</div>
                                    <h3>$ 20.0</h3>
                                </div>
                                <div className="mt-4">
                                    <h6>Color</h6>
                                    <Button color="dark" className="col rounded-pill mr-2">&nbsp;</Button>
                                    <Button color="danger" className="col rounded-pill mr-2">&nbsp;</Button>
                                    <Button color="primary" className="col rounded-pill mr-2">&nbsp;</Button>
                                    <Button color="success" className="col rounded-pill mr-2">&nbsp;</Button>
                                </div>
                                <div className="mt-5">
                                    <Row>
                                        <Col md={3}>
                                            <h6 className="ml-2">Size</h6>
                                            <Counter />
                                        </Col>
                                        <Col md={3}>
                                            <h6 className="ml-2">Jumlah</h6>
                                            <Counter />
                                        </Col>
                                    </Row>
                                </div>
                                <div className="mt-4">
                                    <Row>
                                        <Col md={3}>
                                            <div>
                                            <Button color="white" className="btn btn-outline-secondary rounded-pill but" size="lg" block>Chat</Button>
                                            </div>
                                        </Col>
                                        <Col md={3}><Button color="white" className="btn btn-outline-secondary rounded-pill but">Add bag</Button></Col>
                                        <Col md={6}><Button color="danger" className="rounded-pill but">Buy Now</Button></Col>
                                    </Row>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    </div>
                    <div className="mb-4 mt-5">
                        <h2 className="mb-4">Informasi Produk</h2>
                        <h4>Condition</h4>
                        <h6 className="text-danger mb-4">New</h6>
                        <h4>Description</h4>
                        <article className="text-secondary mb-4">Lorem ipsum dolor sit amet, consectetur adipiscing elit. <br/><br/> Donec non magna rutrum, pellentesque augue eu, sagittis velit. Phasellus quis laoreet dolor. Fusce nec pharetra quam. Interdum et malesuada fames ac ante ipsum primis in faucibus. Praesent sed enim vel turpis blandit imperdiet ac ac felis. Etiam tincidunt tristique placerat. Pellentesque a consequat mauris, vel suscipit ipsum. Donec ac mauris vitae diam commodo vehicula. Donec quam elit, sollicitudin eu nisl at, ornare suscipit magna. <br /><br />Donec non magna rutrum, pellentesque augue eu, sagittis velit. Phasellus quis laoreet dolor. Fusce nec pharetra quam. Interdum et malesuada fames ac ante ipsum primis in faucibus. Praesent sed enim vel turpis blandit imperdiet ac ac felis. <br /><br /> In ultricies rutrum tempus. Mauris vel molestie orci.</article>
                        <h3>Product review</h3>
                    </div>
                    <hr />
                    <div className="mt-5 mb-5">
                        <div className="h4">You can also like this</div>
                        <div className="text-secondary mt-2">You've never seen it before</div>
                    </div>
                    <Row>
                    {!isLoading && !isError && data.length!==0 && data.map(item => {
                        return(
                        <Col sm={4} md={3}>
                            <Card className="mt-3 mr-3 shadow">
                                <CardImg className="img-fluid" width="100%" src={pol} alt="pol"/>
                               <CardBody>
                                    <CardTitle>                                       
                                        {item.name}
                                    </CardTitle>
                                    <div>
                                    <CardSubtitle className="text-danger mt-1">
                                        Rp.{item.price}
                                    </CardSubtitle>
                                    <div className="text-secondary mt-1">
                                        Blanja cloth
                                    </div>
                                    <div className="mt-1">
                                        <span><img src={star} alt="star"/></span>
                                        <span className="ml-1"><img src={star} alt="star"/></span>
                                        <span className="ml-1"><img src={star} alt="star"/></span>
                                        <span className="ml-1"><img src={star} alt="star"/></span>
                                        <span className="ml-1"><img src={star} alt="star"/></span>
                                        <span className="text-secondary ml-1">(10)</span>
                                    </div>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
                        )
                        })}
                    </Row>
                </Container>
            </>
        )
    }
}

const mapStateToProps = state => ({
    homeAction: state.home,
    cart: state.cart
  })
  
  const mapDispatchToProps = {
    getHome: homeAction.getProduct,
    addCart: cartAction.addCart
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(Detail)