import React from 'react'
import { Container, Navbar, Row, Col,
  NavbarBrand, NavbarToggler, Nav, 
  Collapse, NavItem, NavLink, Button
} from 'reactstrap';
import logo from '../assets/img/logo.png'
import '../App.css'
import {Link} from 'react-router-dom'

class Navibar extends React.Component{
    constructor(props){
        super(props)
        this.state = {
          navbarOpen: false
        }
      }

    render(){
        return(
            <Navbar color="light" light expand="md shadow">
            <Container>
            <Link to="/"><NavbarBrand><img src={logo} alt='logo'/></NavbarBrand></Link>
                <NavbarToggler onClick={()=>this.setState({navbarOpen: !this.state.navbarOpen})}/>
                <Collapse navbar isOpen={this.state.navbarOpen}>
                <Nav navbar className="ml-auto">
                <NavItem>
                    <NavLink><Link to="/login"><Button color="danger" className="but badge-pill">Login</Button></Link></NavLink>
                </NavItem>
                <NavItem>
                    <NavLink><Link to="/register"><Button className="but badge-pill" outline color="secondary">Sign Up</Button></Link></NavLink>
                </NavItem>
                </Nav>
                </Collapse>
                </Container>
            </Navbar>

        )
    }
}

export default Navibar